@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-primary text-white;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  ::-webkit-scrollbar {
    @apply w-2;
  }

  ::-webkit-scrollbar-track {
    @apply bg-primary;
  }

  ::-webkit-scrollbar-thumb {
    @apply bg-accent/50 rounded-full hover:bg-accent;
  }
}

/* Hide scrollbar but keep functionality */
.hide-scrollbar {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

.hide-scrollbar::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded-md transition-all duration-300 ease-in-out font-medium;
  }
  .btn-primary {
    @apply btn bg-accent hover:bg-accent/90 shadow-lg hover:shadow-accent/50;
  }
  .card {
    @apply bg-secondary p-6 rounded-lg shadow-lg hover:shadow-xl transition-all duration-300;
  }
  .input {
    @apply bg-secondary/50 border border-gray-600 rounded-md px-4 py-2 focus:outline-none focus:ring-2 focus:ring-accent;
  }
  .glass-effect {
    @apply backdrop-blur-lg bg-white/10 border border-white/20;
  }
  .gradient-text {
    @apply bg-clip-text text-transparent bg-gradient-to-r from-accent to-purple-500;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

@keyframes pulse-subtle {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

.animate-pulse-subtle {
  animation: pulse-subtle 3s ease-in-out infinite;
}
